.running, .running + span {
  color: rgb(91, 204, 150)!important;
}

.stopped, .stopped + span {
  color: rgb(39, 41, 54)!important;
}

.info, .info + span {
  color: rgb(191, 191, 191)!important;
}

.view {
  font-size: 20px;
  width: 36px;
  height: 36px;
  text-align: center;
  line-height: 36px;
  border-radius: 3px;
  cursor: pointer;
}

.log {
  font-size: 23px;
  width: 36px;
  height: 36px;
  text-align: center;
  line-height: 36px;
  border-radius: 3px;
  cursor: pointer;
}

.success, .success + span {
  color: rgb(88, 203, 148)!important;
}

.run-error, .run-error + span {
  color: rgb(254, 127, 0)!important;
}

.draft, .draft + span {
  color: rgb(254, 127, 0)!important;
}

.filter-btn {
  font-size: 20px;
  width: 36px;
  height: 36px;
  text-align: center;
  line-height: 36px;
  border: 1px solid #3e8feb;
  border-radius: 3px;
  cursor: pointer;
}



